import styled from 'react-emotion'

const Container = styled.div({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  height: '100vh',
  width: '100vw',
  backgroundImage: "url('/confetti-bluegray.svg')",
  backgroundRepeat: 'repeat'
})

export default Container



